// frontend/src/app/questionarios/[id]/page.tsx
'use client';

import { useEffect, useState } from "react";
import api from "@/services/api";
import { useRouter, useParams } from "next/navigation";
import "../globals.css";
import AdminAuthGuard from '@/components/auth/AdminAuthGuard'; // Assumindo que você quer proteger esta página

// --- Interfaces Atualizadas ---
interface Opcao {
    id?: number; // Opcional para novas opções
    texto: string;
    // Adicionaremos um client-side ID temporário para novas opções
    tempId?: string; 
}

interface PerguntaAninhada {
    id?: number; // Opcional para novas perguntas
    enunciado: string;
    tipos: 'TEXTO' | 'MULTIPLA_ESCOLHA';
    opcoes: Opcao[];
    // Adicionaremos um client-side ID temporário para novas perguntas
    tempId?: string; 
}

interface QuePerg {
    // id?: number; // ID da relação QuePerg, pode ser útil se você for editar a relação em si
    perguntaId?: number; // ID da pergunta original (se já existir)
    questionarioId: number; // ID do questionário atual
    pergunta: PerguntaAninhada;
}

// Interface para o Questionário como vem da API (apenas título)
interface QuestionarioData {
    id: number;
    titulo: string;
    // Se o backend retornar o criador, adicione aqui
    // criador?: { nome: string; email: string; };
}

export default function EditQuestionarioPage() {
    return (
        <AdminAuthGuard>
            <EditQuestionarioForm />
        </AdminAuthGuard>
    )
}

function EditQuestionarioForm() {
    const router = useRouter();
    const { id } = useParams();
    const questionarioId = Number(id);

    const [titulo, setTitulo] = useState("");
    const [quePergs, setQuePergs] = useState<QuePerg[]>([]); // Agora armazena as associações
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        if (!questionarioId) {
            setIsLoading(false);
            setError("ID do questionário não fornecido.");
            return;
        }
        setIsLoading(true);
        setError(null);

        const loadData = async () => {
            try {
                const respQuestionario = await api.get<QuestionarioData>(`/questionarios/${questionarioId}`);
                setTitulo(respQuestionario.data.titulo);

                const respQuePerg = await api.get<QuePerg[]>(`/quePerg?questionarioId=${questionarioId}`);
                const sanitizedQuePergs = respQuePerg.data.map(qp => ({
                    ...qp,
                    pergunta: {
                        ...qp.pergunta,
                        opcoes: qp.pergunta.opcoes || []
                    }
                }));
                setQuePergs(sanitizedQuePergs);

            } catch (err: any) {
                console.error("Erro ao carregar dados:", err);
                if (err.response && (err.response.status === 401 || err.response.status === 403)) {
                    setError("Acesso não autorizado ou negado.");
                    // O AdminAuthGuard deve cuidar do redirecionamento
                } else if (err.response && err.response.status === 404) {
                    setError("Questionário não encontrado.");
                }
                else {
                    setError("Não foi possível carregar os dados para edição.");
                }
            } finally {
                setIsLoading(false);
            }
        };
        loadData();
    }, [questionarioId]);

    const handlePerguntaChange = (index: number, novoEnunciado: string) => { /* ... (igual ao anterior) ... */ };
    const handleTipoChange = (index: number, novoTipo: 'TEXTO' | 'MULTIPLA_ESCOLHA') => { /* ... (igual ao anterior) ... */ };
    const handleOptionChange = (qIndex: number, oIndex: number, novoTexto: string) => { /* ... (igual ao anterior) ... */ };
    const addOptionToList = (qIndex: number) => { /* Renomeado de addOption para evitar conflito de nome */
        const newState = [...quePergs];
        // Gerar um tempId para a nova opção para a key do React
        newState[qIndex].pergunta.opcoes.push({ texto: '', tempId: `temp-opt-${Date.now()}` });
        setQuePergs(newState);
    };
    const removeOption = (qIndex: number, oIndex: number) => { /* ... (igual ao anterior) ... */ };

    // --- NOVA FUNÇÃO PARA ADICIONAR UMA PERGUNTA AO ESTADO ---
    const handleAddNewPergunta = () => {
        const novaPerguntaDefault: PerguntaAninhada = {
            // Não tem 'id' ainda, pois não foi salva no banco
            tempId: `temp-perg-${Date.now()}`, // ID temporário para a key do React
            enunciado: "",
            tipos: "TEXTO",
            opcoes: []
        };
        const novoQuePerg: QuePerg = {
            questionarioId: questionarioId, // Associa ao questionário atual
            pergunta: novaPerguntaDefault
        };
        setQuePergs(prevQuePergs => [...prevQuePergs, novoQuePerg]);
    };
    // ----------------------------------------------------------
    
    const handleSaveChanges = async (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault();
        setIsLoading(true);
        setError(null);
        try {
            // 1. Salvar o título do questionário
            await api.put(`/questionarios`, {
                id: questionarioId,
                titulo: titulo
            });

            // 2. Processar cada pergunta (QuePerg)
            for (const qp of quePergs) {
                let perguntaData = { ...qp.pergunta };
                let perguntaSalvaOuAtualizada: PerguntaAninhada;

                if (perguntaData.id) { // Pergunta existente, ATUALIZAR
                    const updatePayload = {
                        id: perguntaData.id,
                        enunciado: perguntaData.enunciado,
                        tipos: perguntaData.tipos,
                        opcoes: perguntaData.opcoes.map(opt => ({ texto: opt.texto })) // Envia apenas o texto
                    };
                    const response = await api.put(`/perguntas`, updatePayload);
                    perguntaSalvaOuAtualizada = response.data;
                } else { // Nova pergunta, CRIAR
                    const createPayload = {
                        enunciado: perguntaData.enunciado,
                        tipos: perguntaData.tipos,
                        // Envia as opções se for múltipla escolha
                        opcoes: perguntaData.tipos === 'MULTIPLA_ESCOLHA' ? perguntaData.opcoes.map(opt => opt.texto) : [] 
                    };
                    const response = await api.post(`/perguntas`, createPayload);
                    perguntaSalvaOuAtualizada = response.data;

                    // Associar a nova pergunta criada ao questionário atual
                    await api.post('/queperg', {
                        questionario_id: questionarioId,
                        pergunta_id: perguntaSalvaOuAtualizada.id
                    });
                }
            }

            alert("Questionário e perguntas atualizados com sucesso!");
            // Opcional: Recarregar os dados para refletir IDs de novas perguntas/opções
            // ou simplesmente redirecionar.
            router.push("/questionarios"); 

        } catch (err: any) {
            console.error('Erro ao salvar:', err.response?.data || err.message);
            setError(err.response?.data?.message || 'Erro ao salvar as alterações!');
        } finally {
            setIsLoading(false);
        }
    };
    
    if (isLoading && !error && quePergs.length === 0 && !titulo) { // Ajuste na condição de loading
        return <div className="page-container center-content"><p>Carregando dados do questionário...</p></div>;
    }

    if (error && quePergs.length === 0 && !titulo) { // Mostrar erro se não conseguir carregar nada
        return <div className="page-container center-content"><p style={{color: 'red'}}>{error}</p></div>;
    }

    return (
        <div className="page-container">
            <form onSubmit={handleSaveChanges} className="editor-form-card">
                <div className="form-header">
                    <h3>Editando Questionário</h3>
                    <div className="form-header-actions">
                        <button type="button" onClick={() => router.push("/questionarios")} className="btn-secondary" disabled={isLoading}>Cancelar</button>
                        <button type="submit" className="btn-primary" disabled={isLoading}>
                            {isLoading ? "Salvando..." : "Salvar Alterações"}
                        </button>
                    </div>
                </div>

                <div className="display-section">
                    <label htmlFor="titulo-input">Título do Questionário</label>
                    <input 
                        id="titulo-input" 
                        type="text" 
                        value={titulo} 
                        onChange={(e) => setTitulo(e.target.value)} 
                        className="input-edit-mode title-input"
                        disabled={isLoading}
                    />
                </div>

                <div className="display-section">
                    <label>Perguntas do Questionário</label>
                    <div className="perguntas-edit-list">
                        {quePergs.map((qp, qIndex) => (
                            // Usar tempId para novas perguntas, ou pergunta.id para existentes
                            <div key={qp.pergunta.id || qp.pergunta.tempId} className="pergunta-editor-item">
                                <textarea
                                    value={qp.pergunta.enunciado}
                                    onChange={(e) => handlePerguntaChange(qIndex, e.target.value)}
                                    className="input-edit-mode question-textarea"
                                    rows={2}
                                    placeholder="Digite o enunciado da pergunta"
                                    disabled={isLoading}
                                />
                                <div className="pergunta-meta-editor">
                                    <label>Tipo de Pergunta</label>
                                    <select 
                                        value={qp.pergunta.tipos} 
                                        onChange={(e) => handleTipoChange(qIndex, e.target.value as 'TEXTO' | 'MULTIPLA_ESCOLHA')} 
                                        className="select-tipo-pergunta"
                                        disabled={isLoading}
                                    >
                                        <option value="TEXTO">Texto</option>
                                        <option value="MULTIPLA_ESCOLHA">Múltipla Escolha</option>
                                    </select>
                                </div>

                                {qp.pergunta.tipos === 'MULTIPLA_ESCOLHA' && (
                                    <div className="opcoes-editor-container">
                                        <label>Opções de Resposta</label>
                                        {qp.pergunta.opcoes.map((opt, oIndex) => (
                                            <div key={opt.id || opt.tempId || `q${qIndex}-o${oIndex}`} className="opcao-editor-item">
                                                <input
                                                    type="text"
                                                    value={opt.texto}
                                                    onChange={(e) => handleOptionChange(qIndex, oIndex, e.target.value)}
                                                    placeholder={`Opção ${oIndex + 1}`}
                                                    className="input-edit-mode"
                                                    disabled={isLoading}
                                                />
                                                <button 
                                                    type="button" 
                                                    onClick={() => removeOption(qIndex, oIndex)} 
                                                    className="btn-remover-opcao"
                                                    disabled={isLoading}
                                                >
                                                    &times;
                                                </button>
                                            </div>
                                        ))}
                                        <button 
                                            type="button" 
                                            onClick={() => addOptionToList(qIndex)} 
                                            className="btn-adicionar-opcao"
                                            disabled={isLoading}
                                        >
                                            + Adicionar Opção
                                        </button>
                                    </div>
                                )}
                            </div>
                        ))}
                        {/* --- BOTÃO PARA ADICIONAR NOVA PERGUNTA --- */}
                        <button 
                            type="button" 
                            onClick={handleAddNewPergunta} 
                            className="btn-adicionar-opcao" // Pode criar um estilo diferente se quiser
                            style={{marginTop: '1rem', backgroundColor: 'var(--primary-color)', color: 'white', border: 'none'}}
                            disabled={isLoading}
                        >
                            + Adicionar Nova Pergunta ao Questionário
                        </button>
                        {/* ----------------------------------------- */}
                    </div>
                </div>
            </form>
        </div>
    );
}